

* {
  font-family: "Open Sans", sans-serif;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #616262;
}

.header p {
  color: #696969;
  font-size: 10px;
  line-height: 16px;
}

.header .icon-mail {
  width: 14%;
  max-width: 30px;
  margin-bottom: 5px;
}

.header .icon-telephone {
  max-width: 30px;
  margin-bottom: 5px;
  width: 12%;
}

.header .icon-web {
  max-width: 30px;
  width: 12%;
}

.header-bottom .invoice-header table {
  width: 100%;
  border: none;
}

.invoicePrint .colorgreen {
  color: #7dbb77 !important;
}

.header-bottom .invoice-header table thead td {
  background: #fff;
  padding: 0 0 10px 8px;
  vertical-align: bottom;
}

.header-bottom .invoice-header table tbody td {
  padding: 20px;
  background: url("/assets/images/invoice/arrow.png") no-repeat 20px top #7dbb77;
  font-size: 16px;
  color: #fff;
}

.header-bottom .invoice-header table .circle {
  width: 60px;
  height: 60px;
  border: 1px solid #afafb0;
  border-radius: 36px 36px 36px 36px;
  padding: 15px 11px;
}

.header-bottom .invoice-header li {
  float: right;
}

.header-bottom .invoice-header p {
  padding: 20px 32px;
  background: #7dbb77;
  color: #fff;
  print-color-adjust: exact;
}

.header-bottom .icon-invoice {
  width: 5%;
  min-width: 22px;
}

.header-bottom .icon-mail {
  margin-right: 10px;
  min-width: 22px;
  width: 4%;
}

.header-bottom .icon-mobile {
  margin-left: 5px;
  margin-right: 16px;
  width: 2%;
  min-width: 12px;
}

.products-table {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 0;
  border: none;
}

.products-table h5 {
  margin: 0;
  font-weight: bold;
  color: #616262;
}

.products-table th {
  padding: 5px;
  background: #fff;
  font-size: 16px;
  color: #7dbb77;
  text-align: center;
  print-color-adjust: exact;


}

.products-table th:first-child {
  text-align: left;
}

.products-table tbody tr:nth-child(odd) td {
  background: #e8e8e9;
}

.products-table tbody td {
  padding: 20px;
  vertical-align: middle;
  color: #616262;
  text-align: center;
}

.products-table tbody td:first-child {
  text-align: left;
}

.products-table tbody p {
  margin: 0;
}

.totals table {
  width: 100%;
  border: none;
}

.totals table tr td {
  background: #fff;
  font-size: 18px;
  text-align: right;
}

.totals table tr td:last-child {
  padding-right: 20px;
}

.totals table tfoot td {
  padding: 16px 10px;
  background: #7dbb77;
  color: #fff;
  font-size: 24px;
}

@media print {
  .print-exact {
    print-color-adjust: exact;
    -webkit-print-color-adjust: exact; 
  }
}

.totals table .discount td {
  font-weight: bold;
  color: red;
}

.totals .signature {
  text-align: center;
}

.totals .signature .icon-signature {
  width: 40%;
  margin-bottom: 20px;
}

.totals .signature p {
  margin: 0;
}

.bottom-left {
  font-size: 12px;
}

.bottom-left table {
  border: none;
}

.bottom-left table thead th {
  background: #acacac;
  font-weight: normal;
  color: #fff;
  border-radius: 0px 0px 5px 5px;
  -moz-border-radius: 0px 0px 5px 5px;
  -webkit-border-radius: 0px 0px 5px 5px;
}

.bottom-left table tbody td {
  padding-top: 30px;
  padding-bottom: 0;
  background: #fff;
  font-size: 18px;
  color: #8f8f8f;
}

.bottom-left p {
  margin: 0;
  font-size: 12px;
  line-height: 14px;
}

.terms {
  margin-top: 100px;
  font-size: 12px;
}

.footer-invoice {
  padding: 30px 0;
  background: url("/assets/images/invoice/footer.png") no-repeat top;
  font-size: 14px;
  color: #ababab;
}

.footer-invoice p {
  height: 50px;
  font-size: 11px;
  line-height: 14px;
  border-right: 1px solid #d4d2d2;
}

.footer-invoice img {
  width: 60px;
}